







































.m-geo {
    .city {
        display: inline-block;

        .change-city {
            border: 1px solid #E5E5E5;
            color: #666;
            margin: 0 4px;

            &:hover {
                color: #41B883;
            }
        }

        .near-city {
            display: inline-block;

            a {
                color: #999;
                margin: 0 4px;

                &:hover {
                    color: #41B883;
                }
            }
        }
    }

    .user-enter {
        display: inline-block;
        margin-left: 15px;

        a {
            margin-left: 10px;
            color: #666;

            &:hover {
                color: #41B883;
            }
        }

        .login {
            color: #41B883;
        }

    }


}

