










































































































































.m-nav {
    float: right;
    height: 40px;

    li {
        float: left;
        width: 78px;
        line-height: 40px;
        text-align: center;
        box-sizing: border-box;
        cursor: pointer;

        &:hover {
            color: #41B883;
        }

        &.list {
            dl {
                background: #fff;
                display: none;
                box-shadow: 0 3px 5px 0px rgba(0, 0, 0, .1);
            }

            &:hover {
                box-shadow: 0 3px 5px 0px rgba(0, 0, 0, .1);
                background: #fff;

                dl {
                    display: block;
                }
            }
        }

        &.bd {
            dl {
                width: 140%;
                position: relative;
                left: -31px;
                top: -1px;
            }
        }

        &.site {

            &:hover {
                background-color: #fff;
                box-shadow: 0 3px 5px 0px rgba(0, 0, 0, .1);

                .subContainer {
                    display: flex;
                }
            }

            .subContainer {
                cursor: default;
                display: none;
                width: 1200px;
                box-sizing: border-box;
                padding: 30px 36px 36px 47px;
                position: relative;
                left: -1200+78px;
                background-color: #fff;
                box-shadow: 0 3px 5px 0px rgba(0, 0, 0, .1);
                border-top-left-radius: 4px;

                dl {
                    margin-right: 47px;

                    dd {
                        cursor: pointer;
                    }
                }

                dt {
                    line-height: 20px;
                    height: 20px;
                    font-size: 14px;
                    margin-bottom: 26px;
                }


                .hotel {
                    width: 234px;

                    dd {
                        width: 33.33%;
                        float: left;
                    }
                }

                .food {
                    width: 156px;

                    dd {
                        width: 50%;
                        float: left;
                    }
                }

                .movie {
                    width: 90px;
                }

                .app {
                    width: 380px;
                    flex: 1;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-content: flex-start;

                    dt {
                        width: 100%;
                    }

                    dd {
                        margin: 0 8px;

                        img {
                            width: 60px;
                            height: 60px;
                        }
                    }
                }

                &::after {
                    content: "";
                    display: block;
                    clear: both;
                }
            }
        }

    }


}
