.layout-blank {
    background-color: #fff;
    color: #2e2f30;
    letter-spacing: 0.5px;
    font-family: "Source Sans Pro", Arial, sans-serif;
    height: 100vh;
    margin: 0;
}

body {
    margin: 0;
    padding: 0;
}

body * {
    margin: 0;
    padding: 0;
    list-style: none;
}

footer {
    text-align: center;
}

a:hover, a:focus {
    color: #41B883;
}

a {
    color: #999;
}

a:link {
    text-decoration: none;
}

.logo {
    width: 243px;
    height: 218px;
}

.page-enter-active, .page-leave-active {
    transition: opacity .5s
}

.page-enter, .page-leave-active {
    opacity: 0
}
