









































































































































































.footer-content {
    text-align: left;
    width: 100%;
    a {
        color: #666666;

        &:hover {
            color: #31BBAC;
        }
    }
}

.footer-link {
    width: 100%;
    box-sizing: border-box;
    border-top: 1px solid #E5E5E5;
    border-bottom: 1px solid #E5E5E5;

    padding: 40px 45px;
    display: flex;

    .footer-column {
        width: 220px;

        dl {
            padding-bottom: 20px;

            dt {
                line-height: 28px;
                color: #666666;
                font: {
                    size: 14px;
                    weight: 700;
                }
            }

            dd {
                line-height: 24px;
                color: #666666;
                font: {
                    size: 12px;
                }
            }
        }
    }
}

.footer-copyright {
    width: 100%;
    height: 76px;
    padding: 15px 0 30px 20px;
    a {
        color: #999999;
        margin-right: 10px;
        &:hover {
            color: #31BBAC;
        }
    }
    font-size: 12px;
    color: #999999;
    .footer-copyright-left {
        float: left;
    }
    .footer-copyright-right {
        float: right;
    }
}
